___CSS_LOADER_EXPORT___.push([module.id, `.usage-dashboard {
    background: var(--vscode-sideBar-background);
    border-bottom: 1px solid var(--vscode-panel-border);
    padding: 16px;
    animation: slideDown 0.2s ease;
}

.dashboard-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
}

.dashboard-header h3 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
}

.stats-summary {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
    margin-bottom: 16px;
}

.stat-card {
    padding: 16px;
    background: linear-gradient(135deg, rgba(255, 87, 34, 0.1), rgba(255, 152, 0, 0.1));
    border-radius: 12px;
    text-align: center;
}

.stat-value {
    font-size: 24px;
    font-weight: 700;
    background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin-bottom: 4px;
}

.stat-label {
    font-size: 12px;
    opacity: 0.7;
}

.provider-stats {
    display: flex;
    flex-direction: column;
    gap: 8px;
    max-height: 300px;
    overflow-y: auto;
}

.provider-stat {
    padding: 12px;
    background: var(--vscode-editor-background);
    border-radius: 8px;
    border-left: 3px solid var(--primary-color);
}

.provider-name {
    font-weight: 600;
    margin-bottom: 4px;
}

.provider-details {
    display: flex;
    gap: 12px;
    font-size: 12px;
    opacity: 0.8;
    margin-bottom: 4px;
}

.provider-last-used {
    font-size: 11px;
    opacity: 0.6;
}

.no-stats {
    text-align: center;
    padding: 32px;
    opacity: 0.6;
}

.no-stats p {
    margin: 4px 0;
}

.no-stats .hint {
    font-size: 12px;
    opacity: 0.7;
}

.provider-stats::-webkit-scrollbar {
    width: 6px;
}

.provider-stats::-webkit-scrollbar-track {
    background: transparent;
}

.provider-stats::-webkit-scrollbar-thumb {
    background: var(--primary-color);
    border-radius: 3px;
}`, "",{"version":3,"sources":["webpack://./src/webview/components/UsageDashboard.css"],"names":[],"mappings":"AAAA;IACI,4CAA4C;IAC5C,mDAAmD;IACnD,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,mBAAmB;AACvB;;AAEA;IACI,SAAS;IACT,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mFAAmF;IACnF,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,8EAA8E;IAC9E,6BAA6B;IAC7B,oCAAoC;IACpC,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,2CAA2C;IAC3C,kBAAkB;IAClB,2CAA2C;AAC/C;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,eAAe;IACf,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,gCAAgC;IAChC,kBAAkB;AACtB","sourcesContent":[".usage-dashboard {\n    background: var(--vscode-sideBar-background);\n    border-bottom: 1px solid var(--vscode-panel-border);\n    padding: 16px;\n    animation: slideDown 0.2s ease;\n}\n\n.dashboard-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin-bottom: 16px;\n}\n\n.dashboard-header h3 {\n    margin: 0;\n    font-size: 16px;\n    font-weight: 600;\n}\n\n.stats-summary {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 12px;\n    margin-bottom: 16px;\n}\n\n.stat-card {\n    padding: 16px;\n    background: linear-gradient(135deg, rgba(255, 87, 34, 0.1), rgba(255, 152, 0, 0.1));\n    border-radius: 12px;\n    text-align: center;\n}\n\n.stat-value {\n    font-size: 24px;\n    font-weight: 700;\n    background: linear-gradient(135deg, var(--primary-color), var(--accent-color));\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n    background-clip: text;\n    margin-bottom: 4px;\n}\n\n.stat-l