___CSS_LOADER_EXPORT___.push([module.id, `.chat-interface {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: var(--vscode-editor-background);
    position: relative;
    font-family: var(--vscode-font-family);
}

.chat-interface.compact {
    font-size: 13px;
}

/* --- Header --- */
.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 16px;
    background: var(--vscode-editor-background);
    /* Keep it solid or subtle */
    border-bottom: 1px solid var(--vscode-panel-border);
    height: 50px;
    box-sizing: border-box;
    z-index: 10;
}

.header-left {
    display: flex;
    align-items: center;
    gap: 12px;
}

.header-actions {
    display: flex;
    align-items: center;
    gap: 8px;
}

.separator-vertical {
    width: 1px;
    height: 20px;
    background: var(--vscode-panel-border);
    margin: 0 4px;
}

.icon-btn {
    background: transparent;
    border: none;
    border-radius: 6px;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--vscode-icon-foreground);
    cursor: pointer;
    transition: all 0.2s;
    opacity: 0.7;
}

.icon-btn:hover {
    background: var(--vscode-toolbar-hoverBackground);
    opacity: 1;
}

.icon-btn.danger:hover {
    background: rgba(255, 59, 48, 0.1);
    color: #ff3b30;
}

/* --- Dashboard Overlay --- */
.dashboard-overlay {
    position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--vscode-editor-background);
    z-index: 20;
    overflow-y: auto;
    animation: fadeIn 0.15s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}


/* --- Main Content --- */
/* MessageList is handled by its own component but should fill space */
.message-list {
    flex: 1;
    overflow-y: auto;
}


/* --- Footer --- */
.chat-footer {
    padding: 16px;
    background: var(--vscode-editor-background);
    border-top: 1px solid var(--vscode-panel-border);
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.model-bar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-left: 2px;
}

/* --- Responsive --- */
@media (max-width: 600px) {
    .chat-footer {
        padding: 12px;
    }
