___CSS_LOADER_EXPORT___.push([module.id, `:root {
    /* VibeAll Premium Palette - Dark Mode Optimized */
    --primary-color: #6366f1; /* Indigo */
    --primary-hover: #4f46e5;
    --accent-color: #8b5cf6; /* Violet */
    --accent-hover: #7c3aed;
    --success-color: #10b981;
    --error-color: #ef4444;
    --warning-color: #f59e0b;
    
    /* Neumorphic / Glassmorphic Bases */
    --glass-bg: rgba(30, 30, 30, 0.4);
    --glass-border: rgba(255, 255, 255, 0.08);
    --glass-blur: 12px;
    
    /* Shadows */
    --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.1);
    --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.2);
    --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.3);
    
    /* Animations */
    --ease-out-cubic: cubic-bezier(0.215, 0.61, 0.355, 1);
    --anim-duration-fast: 0.2s;
    --anim-duration-normal: 0.3s;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
    overflow: hidden;
    background: transparent; /* Let VS Code background show */
}

/* Premium Scrollbar */
::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

::-webkit-scrollbar-track {
    background: transparent;
}

::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 3px;
    transition: background 0.2s;
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.3);
}

.app-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: linear-gradient(
        180deg, 
        rgba(0,0,0,0) 0%, 
        rgba(0,0,0,0.2) 100%
    ); /* Subtle depth */
    color: var(--vscode-editor-foreground);
    position: relative;
    overflow: hidden;
}

/* Toast Container - Floating */
.toast-container {
    position: fixed;
    top: 24px;
    right: 24px;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    gap: 12px;
    pointer-events: none;
    perspective: 1000px;
}

.toast-container > * {
    pointer-events: all;
    animation: slideInRight 0.4s var(--ease-out-cubic);
}

/* Typography Enhancements */
h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
    letter-spacing: -0.02em;
}

button {
    font-family: inherit;
    cursor: pointer;
    user-select: none;
}

/* Global Animations */
@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(20px) scale(0.95);
    }
    to {
        opacity: 1;
        transform: translateX(0) scale(1);
    }
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes scaleIn {
    from { 
        opacity: 0; 
        transform: scale(0.95);
    }
    to { 
        opacity: 1; 
        transform: scale(1);
    }
}`, "",{"version":3,"sources":["webpack://./src/webview/style.css"],"names":[],"mappings":"AAAA;IACI,kDAAkD;IAClD,wBAAwB,EAAE,WAAW;IACrC,wBAAwB;IACxB,uBAAuB,EAAE,WAAW;IACpC,uBAAuB;IACvB,wBAAwB;IACxB,sBAAsB;IACtB,wBAAwB;;IAExB,oCAAoC;IACpC,iCAAiC;IACjC,yCAAyC;IACzC,kBAAkB;;IAElB,YAAY;IACZ,yCAAyC;IACzC,0CAA0C;IAC1C,0CAA0C;;IAE1C,eAAe;IACf,qDAAqD;IACrD,0BAA0B;IAC1B,4BAA4B;AAChC;;AAEA;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,kHAAkH;IAClH,gBAAgB;IAChB,uBAAuB,EAAE,gCAAgC;AAC7D;;AAEA,sBAAsB;AACtB;IACI,UAAU;IACV,WAAW;AACf;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,oCAAoC;IACpC,kBAAkB;IAClB,2BAA2B;AAC/B;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB;;;;KAIC,EAAE,iBAAiB;IACpB,sCAAsC;IACtC,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA,+BAA+B;AAC/B;IACI,eAAe;IACf,SAAS;IACT,WAAW;IACX,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,kDAAkD;AACtD;;AAEA,4BAA4B;AAC5B;IACI,gBAAgB;IAChB,uBAAuB;AAC3B;;AAEA;IACI,oBAAoB;IACpB,eAAe;IACf,iBAAiB;AACrB;;AAEA,sBAAsB;AACtB;IACI;QACI,UAAU;QACV,uCAAuC;IAC3C;IACA;QACI,UAAU;QACV,iCAAiC;IACrC;AACJ;;AAEA;IACI,OAAO,UAAU,EAAE;IACnB,KAAK,UAAU,EAAE;AACrB;;AAEA;IACI;QACI,UAAU;QACV,sBAAsB;IAC1B;IACA;QACI,UAAU;QACV,mBAAmB;IACvB;AACJ","sourcesContent":[":root {\n    /* VibeAll Premium Palette - Dark Mode Optimized */\n    --primary-color: #6366f1; /* Indigo */\n    --primary-hover: #4f46e5;\n    --accent-color: #8b5cf6; /* Violet */\n    --accent-hover: #7c3aed;\n    --success-color: #10b981;\n    --error-color: #ef4444;\n    --warning-color: #f59e0b;\n    \n    /* Neumorphic / Glassmorphic Bases */\n    --glass-bg: rgba(30, 30, 30, 0.4);\n    --glass-border: rgba(255, 255, 255, 0.08);\n    --glass-blur: 12px;\n    \n    /* Shadows */\n    --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.1);\n    --shadow-md: 0 4px 16px rgba(0, 0, 0, 0.2);\n    --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.3);\n    \n    /* Animations */\n    --ease-out-cubic: cubic-bezier(0.215, 0.61, 0.355, 1);\n    --anim-duration-fast: 0.2s;\