___CSS_LOADER_EXPORT___.push([module.id, `.settings-panel {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: var(--vscode-editor-background);
}

.settings-header {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 12px 20px;
    border-bottom: 1px solid var(--vscode-panel-border);
    background: var(--vscode-editor-background);
}

.back-button {
    background: transparent;
    border: none;
    color: var(--vscode-foreground);
    padding: 8px;
    border-radius: 6px;
    cursor: pointer;
    font-size: 13px;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 6px;
    transition: background 0.2s;
}

.back-button:hover {
    background: var(--vscode-toolbar-hoverBackground);
}

.settings-header h2 {
    margin: 0;
    font-size: 16px;
    font-weight: 600;
}

.settings-content {
    flex: 1;
    overflow-y: auto;
    padding: 24px;
}

.settings-section {
    margin-bottom: 32px;
}

.settings-section h3 {
    margin: 0 0 16px 0;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--vscode-descriptionForeground);
    letter-spacing: 0.5px;
}

/* Color Grid */
.color-grid {
    display: flex;
    gap: 12px;
    flex-wrap: wrap;
}

.color-box {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: transform 0.2s;
    border: 2px solid transparent;
}

.color-box:hover {
    transform: scale(1.1);
}

.check-mark {
    color: white;
    font-size: 14px;
    font-weight: bold;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
}

/* API Keys */
.api-keys-list {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.api-key-item {
    display: flex;
    flex-direction: column;
    gap: 6px;
    position: relative;
}

.provider-name {
    font-size: 12px;
    font-weight: 500;
}

.key-input-wrapper {
    display: flex;
    gap: 8px;
    align-items: center;
}

.key-input-wrapper input {
    flex: 1;
    padding: 8px 12px;
    background: var(--vscode-input-background);
    border: 1px solid var(--vscode-input-border);
    border-radius: 6px;
    color: var(--vscode-input-foreground);
    font-size: 12px;
    font-family: monospace;
}

.key-input-wrapper input:focus {
    outline: none;
    border-color: var(--primary-color);
}

.action-btn {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--vscode-button-secondaryBackground);
    color: var(--vscode-button-secondaryForeground);
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
}

.action-btn:hover {
    background: var(--vscode-button-secondaryHoverBackground);
}

.save-btn {
    background: var(--primary-color);
    color: white;
}

.save-btn:hover {
    opacity: 0.9;
}

.save-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.save-btn.success {
    background: #4CAF50;
}

.mini-toast {
    position: absolute;
    right: 0;
    top: -20px;
    font-size: 11px;
    color: #4CAF50;
    font-weight: 600;
    animation: fadeUp 0.3s ease;
}

@keyframes fadeUp {
    from {
        opacity: 0;
        transform: translateY(5px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Toggles */
.toggle-group {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.toggle-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    background: var(--vscode-textBlockQuote-background);
    border-radius: 8px;
    cursor: pointer;
}

.toggle-item:hover {
    background: var(--vscode-list-hoverBackground);
}

.settings-footer {
    margin-top: 40px;
    text-align: center;
    font-size: 11px;
    opacity: 0.4;
}`, "",{"version":3,"sources":["webpack://./src/webview/components/SettingsPanel.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,2CAA2C;AAC/C;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,kBAAkB;IAClB,mDAAmD;IACnD,2CAA2C;AAC/C;;AAEA;IACI,uBAAuB;IACvB,YAAY;IACZ,+BAA+B;IAC/B,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,2BAA2B;AAC/B;;AAEA;IACI,iDAAiD;AACrD;;AAEA;IACI,SAAS;IACT,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,OAAO;IACP,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,yBAAyB;IACzB,0CAA0C;IAC1C,qBAAqB;AACzB;;AAEA,eAAe;AACf;IACI,aAAa;IACb,SAAS;IACT,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,0BAA0B;IAC1B,6BAA6B;AACjC;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,iBAAiB;IACjB,yCAAyC;AAC7C;;AAEA,aAAa;AACb;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,mBAAmB;AACvB;;AAEA;IACI,OAAO;IACP,iBAAiB;IACjB,0CAA0C;IAC1C,4CAA4C;IAC5C,kBAAkB;IAClB,qCAAqC;IACrC,eAAe;IACf,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,kCAAkC;AACtC;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,oDAAoD;IACpD,+CAA+C;IAC/C,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,oBAAoB;AACxB;;AAEA;IACI,yDAAyD;AAC7D;;AAEA;IACI,gCAAgC;IAChC,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,UAAU;IACV,eAAe;IACf,cAAc;IACd,gBAAgB;IAChB,2BAA2B;AAC/B;;AAEA;IACI;QACI,UAAU;QACV,0BAA0B;IAC9B;;IAEA;QACI,UAAU;QACV,wBAAwB;IAC5B;AACJ;;AAEA,YAAY;AACZ;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,aAAa;IACb,mDAAmD;IACnD,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,8CAA8C;AAClD;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,eAAe;IACf,YAAY;AAChB","sourcesContent":[".settings-panel {\n    display: flex;\n    flex-direction: column;\n    height: 100vh;\n    background: v